/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
@import url(~antd/dist/antd.css);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block; }

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.chat {
  display: flex;
  height: 100%;
  background-color: #fff;
  width: 1140px;
  margin: 0 auto;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.04204); }
  .chat__sidebar {
    width: 320px;
    border-right: 1px solid #f7f7f7; }
    .chat__sidebar-header {
      display: flex;
      justify-content: space-between;
      padding: 21px;
      height: 58px;
      border-bottom: 1px solid #f7f7f7;
      margin-bottom: 20px;
      align-items: center; }
      .chat__sidebar-header .ant-btn-circle {
        position: relative;
        left: 5px; }
      .chat__sidebar-header > div i {
        margin-right: 5px; }
    .chat__sidebar-dialogs {
      height: calc(100% - 136px); }
      .chat__sidebar-dialogs .dialogs {
        height: 100%;
        overflow: auto; }
        .chat__sidebar-dialogs .dialogs__item {
          padding: 10px 20px;
          margin-bottom: 0;
          cursor: pointer; }
  .chat__dialog {
    flex: 1;
    position: relative; }
    .chat__dialog .ant-empty {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%); }
    .chat__dialog-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 12px;
      border-bottom: 1px solid #f7f7f7; }
      .chat__dialog-header .ant-btn-circle i {
        font-size: 22px; }
      .chat__dialog-header-center {
        text-align: center; }
      .chat__dialog-header-username {
        display: block;
        margin-bottom: 5px;
        font-weight: 500; }

body {
  background-color: #f4f7fd; }

body,
html,
#root,
.wrapper {
  height: 100%;
  color: #202020;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 14px; }

* {
  outline: 0; }

.ant-btn-circle {
  color: #202020 !important;
  opacity: 0.4; }
  .ant-btn-circle:hover {
    opacity: 1; }
